			<Div>
			<InfoRow header="–°–ø–æ—Å–æ–±–Ω–æ—Å—Ç—å –≤–∞—à–µ–≥–æ –≤–∏—Ä—É—Å–∞">
       –í—ã–±–µ—Ä–∏ –æ–¥–Ω—É —Å–ø–æ—Å–æ–±–Ω–æ—Å—Ç—å –¥–ª—è —Ç–≤–æ–µ–≥–æ –≤–∏—Ä—É—Å–∞.
      </InfoRow>
			</Div>
			<FormLayout>
